/**
 * Retrieves all the queued Hiive events.
 *
 * @param {Object} state The current state of the redux store.
 * @return {Array} events An array of events that are queued.
 */
export function getHiiveEventsQueue( state ) {
	return state.hiive.queue.events;
}

/**
 *
 * @param {*} state The current state of the redux store.
 * @return {Array} threshold Threshold of the queue.
 */
export function getHiiveEventsQueueThreshold( state ) {
	return state.hiive.queue.threshold;
}

/**
 * Retrieves the default Hiive URL.
 *
 * @param {Object} state The current state of the redux store.
 * @return {string} The default URL in the store.
 */
export function getHiiveSingleUrl( state ) {
	return state.hiive.urls.single;
}

/**
 * Retrieves the batch Hiive URL.
 *
 * @param {*} state The current state of the redux store.
 * @return {string} The batch URL in the store.
 */
export function getHiiveBatchUrl( state ) {
	return state.hiive.urls.batch;
}

/**
 * Retrieves debounce data.
 *
 * @param {Object} state The current state of the redux store.
 * @return {Object} The debounce data.
 */
export function getHiiveDebounce( state ) {
	return state.hiive.debounce;
}
